export default function Home() {
  return (
    <div style={bg}>
      <div style={card}>
        <h1 style={{ marginBottom: 8 }}>Task Management System</h1>
        <p style={{ color: "#666", marginBottom: 30 }}>
          Organize your tasks efficiently and stay productive
        </p>

        <a href="/login">
          <button style={loginBtn}>Login</button>
        </a>

        <div style={{ marginTop: 20 }}>
          <span style={{ color: "#555" }}>
            Donâ€™t have an account?
          </span>{" "}
          <a href="/register" style={signupLink}>
            Sign up
          </a>
        </div>
      </div>
    </div>
  );
}

/* Styles */

const bg = {
  background: "linear-gradient(135deg, #e0f2fe, #f0f9ff)",
  minHeight: "100vh",
  display: "flex",
  justifyContent: "center",
  alignItems: "center"
};

const card = {
  background: "white",
  padding: 45,
  width: 420,
  borderRadius: 12,
  textAlign: "center" as const,
  boxShadow: "0 10px 25px rgba(0,0,0,.12)"
};

const loginBtn = {
  padding: "12px 40px",
  background: "#2563eb",
  color: "white",
  border: "none",
  borderRadius: 8,
  cursor: "pointer",
  fontSize: 16
};

const signupLink = {
  color: "#2563eb",
  fontWeight: "bold",
  textDecoration: "none"
};
